import * as ts from 'typescript';

export function getPropName(node: ts.JsxAttribute): string {
  if (!node || !node.kind || node.kind !== ts.SyntaxKind.JsxAttribute) {
    throw new Error('The node must be a JSXAttribute collected by the AST parser.');
  }

  return node.name
    ? node.name.text
    : undefined;
}

export function getLiteralText(node: ts.JsxAttribute): string {
  if (!node || node.kind !== ts.SyntaxKind.JsxAttribute) {
    throw new Error('The node must be a JSXAttribute collected by the AST parser.');
  }

  return node.initializer && node.initializer.kind === ts.SyntaxKind.StringLiteral
    ? (node.initializer as ts.StringLiteral).text
    : undefined;
}
